
import { TraceabilityApiFactory } from './types/traceability-api.factory';
import { TraceabilityApiContract } from './types/traceability-api.contract';
import axios from 'axios';


class Trace
{
    private static instance: Trace;

    private _traceabilityModule: TraceabilityApiContract = null;
    private _hasTrace: boolean = false;
    private _traceabilityApiFactory: TraceabilityApiFactory;

    private constructor() 
    {
        this._traceabilityApiFactory = new TraceabilityApiFactory( axios );
    }

    get hasTrace()
    {
        return this._hasTrace;
    }

    public static getInstance(): Trace 
    {
        if (!Trace.instance) {
            Trace.instance = new Trace();
        }
        return Trace.instance;
    }

    public loadTraceabilityModule()
    {
        try{
            this._traceabilityModule = this._traceabilityApiFactory.factoryMethod();
            this._hasTrace = true;
        }
        catch( e )
        {
            this._hasTrace = false;
            console.info('Traza no encontrada', ':::' , e )
        }
    }

    public setMaxScore( maxScore: number )
    {
        if( !this.hasTrace ) return;
        this._traceabilityModule.setMaxScore(maxScore);
    }

    public setMinScore( minScore: number )
    {
        if(!this.hasTrace) return;
        this._traceabilityModule.setMinScore(minScore);
    }

    public setData( data: string ): void
    {
        if(!this.hasTrace) return;
        this._traceabilityModule.setDataToStore( data );
    }

    public getData(): string
    {
        if(! this.hasTrace) return null;
        return this._traceabilityModule.getDataStored();
    }

    public initialize()
    {
        if(!this.hasTrace) return false;
        this._traceabilityModule.initialize();
    }

    public complete( score: number )
    {
        if(!this.hasTrace) return false;
        this._traceabilityModule.complete( score );
    }

    public finish()
    {
        if(!this.hasTrace) return false;
        this._traceabilityModule.finish();
    }
}

let traceInstance = Trace.getInstance();
traceInstance.loadTraceabilityModule();

export let instance = traceInstance;