import { getURLParameter } from '../helpers/manage-url';

export abstract class TraceabilityApiContract
{
    protected _debug: boolean = true;
    protected _startTime:any;
    protected _endTime:any;
    protected _score :any;
    protected _maxScore: number = 100;
    protected _minScore: number = 0;


    public getDebug()
    {
        return this._debug;
    }
    public setDebug( debug: boolean )
    {
        this._debug = debug;
    }

    public getMaxScore()
    {
        return this._maxScore;
    }

    public setMaxScore( score: number )
    {
        this._maxScore = score;
    }

    public getMinScore()
    {
        return this._minScore;
    }
    public setMinScore( score: number )
    {
        this._minScore = score;
    }

    public getScore()
    {
        return this._score;
    }

    public getStartTime()
    {
        return this._startTime;
    }

    public getEndTime()
    {
        return this._endTime;
    }

    abstract initialize():boolean;
    abstract complete( score?:number ):boolean;
    abstract finish():boolean;
    abstract setDataToStore( data: string ): void;
    abstract getDataStored() :string;

    public calculateDuration( startTime: number, endTime:number ): string
    {
        var duration = endTime - startTime ;
            
        var digitalObjectDuration = "";
        var myValue = (Math.floor(duration/3600000));
        
        if ( myValue < 10 ){
            digitalObjectDuration += "0";  
        } 
        digitalObjectDuration += myValue+":" ;
        duration = duration%3600000;
        myValue = (Math.floor(duration/60000));

        if ( myValue < 10 ){
            digitalObjectDuration += "0";  
        } 
        digitalObjectDuration += myValue+":" ;
        duration = duration%60000;
        myValue = (Math.floor(duration/1000));
        if ( myValue < 10 ) {
            digitalObjectDuration += "0";
        }
        digitalObjectDuration += myValue+"." ;
        duration = duration%1000;
        myValue = (Math.floor(duration/10));
        digitalObjectDuration += myValue ;

        return digitalObjectDuration;
    }

    public getDataFromUrl()
    {
        return {
            unitId: getURLParameter('unitId') || "unitX",
            activityId: getURLParameter('activityId')|| "activityX",
            resourceId: getURLParameter('resourceId')|| "resourceX",
        };
    }

    public trace( msg: string)
    {
        if(this.getDebug())
        {
            //Firefox users can use the 'Firebug' extension's console.
            if( window.console )
            {
                console.log(msg);
            } 
            else 
            {
                //alert(msg);
            }
        }
    }

    public makeBoolean( string: string )
    {
        switch(string.toLowerCase()) {
            case "true": case "yes": case "1": return true;
            case "false": case "no": case "0": case null: return false; 
            default: return Boolean(string);
       }     
    }
}