

export function getURLParameter( parametro:string, location?:string ){
    
    var getGET = function( location?:string ) {
        var get:any;
        if( typeof get === "undefined" ){
            get = {};
            var loc = location || document.location.href;
            if( loc.indexOf('?') > 0 ){
                var getString = loc.split('?')[1];
                var GET = getString.split('&');
                for(let i = 0, l = GET.length; i < l; i++){
                    var tmp = GET[i].split('=');
                    get[ tmp[0] ] = unescape( decodeURI(tmp[1]) );
                }
            }
            return get;
        }else{
            return {};
        }
    }
    const response = getGET( location )[ parametro ] || null;
    
    if(response === "false"){
        return false;
    }else if(response === "true"){
        return true;
    }else {
        return response;
    }
}
