import { TraceabilityApiContract } from './traceability-api.contract';
import { getURLParameter } from '../helpers/manage-url';
import { ScormAPI } from './scorm-api';
import { WebcontentAPI } from './webcontent-api';
import { AxiosInstance } from 'axios';
import { AvAPI } from './av-api';


export class TraceabilityApiFactory
{

    constructor( private http: AxiosInstance )
    {
    }
    
    public factoryMethod(): TraceabilityApiContract
    {
        let scorm = getURLParameter('scorm');
        let trackId = getURLParameter('trackId');
        let av = getURLParameter('av');

        if( String(scorm) == 'true')
        {
            return new ScormAPI();
        }
        
        if( trackId && trackId != "" && trackId != "undefined" )
        {
            return new WebcontentAPI( this.http );
        }

        if( String(av) == 'true' )
        {
            return new AvAPI();
        }
        
        throw new Error("No encuentra traza");
    }
}